.cursor{
    cursor: pointer;
}
.tradeHeadingHolder{
    width: 33%;
    display: flex;
    justify-content: center;
    align-items: center;

}

.tradeTabHolder{
    width: 44%;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    background-color: #383737;
    border-radius: 12px;
    margin-left: 36px;

}

.newTradeHeading{
    display: flex;
    width: 100%;
    height: 50px;
    border-bottom: none;
    margin-top: 12px;
}
.tradeTabs{
    width: 40%;
    background-color: black;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 12px;
    height: 30px;
}
.longTradeTab{
    border: 2px solid #3DECA7;   
}
.shortTradeTab{
    border: 2px solid #f34949;   
}

.newTradeContainer{
    border-top: none;
    background-color: #201f1f;
    border-radius: 10px;
    /* height: 95%; */
    width: 90%;
}
.newTradeHolder{
    display: flex;
    justify-content: center;
    /* align-items: center; */
    /* height: 611px; */
}
.greenDot{
    background-color: #17f917;
    border-radius: 100%;
    height: 5px;
    aspect-ratio: 1;
    margin-right: 5px;
}
.priceSlippageContainer{
    display: flex;
    justify-content: space-between;
    margin: 10px 0;
    align-items: center;
}
.priceContainer{
    display: flex;
    flex-direction: column;
}
.slippageContainer{
    display: flex;
    flex-direction: column;
    background: black;
    border: 1px solid #383737;
    border-radius: 10px;
    height: 3rem;
    width: 5rem;
    contain: content;
    align-items: center;
}
.smallText{
    font-size: small;
    color: #82828F;
}
.tradeSummaryHolder{
    display: flex;
    justify-content: center;
    align-items: center;
}
.poolIconClass{
    height: 26px;
    aspect-ratio: 1;
    margin-right: 3px;
}
.marketNewsContainer{
    display: flex;
}
.clickSummaryContainer{
    display: flex;
    flex-direction: column;
}
.confirmTradeButton{
    /* background-color: #2fcd27; */
    width: calc(100% - 13px);
    height: 36px;
    justify-content: center;
    display: flex;
    align-items: center;
    border-radius: 7px;
    font-size: smaller;
}
.selectedLiquidity{
    background: #dfdbdb;
    padding: 0px 12px;
    border-radius: 15px;
    color: #3f5058;
    height: 27px;
}
.addLiquidityButtonHolder{
    display: flex;
    background: #D6BCFA;
    width: 11rem;
    border-radius: 15px;
    height: 2rem;
    align-items: center;
    justify-content: center;
}
.addLiquidityButtonHolder:hover{
    background: #9e68e9;
    cursor: pointer;
}