/*scroll to top */
.scroll-top {
  bottom: 20px;
  font-size: 20px;
  height: 40px;
  position: fixed;
  text-align: center;
  width: 40px;
  z-index: 10;
  cursor: pointer;
  transition: 0.3s;
  border-radius: 50%;
  line-height: 40px;
  right: -100px;
  color: #ffffff;
  background-color: rgba(29, 233, 182, 0.5);
}

.scroll-top:hover {
  background-color: #04d9ff;
  transition: all 0.4s ease-in-out;
}

.scroll-top.active {
