/*theme switcher*/
.switcher-wrap {
  position: fixed;
  top: 250px;
  width: 250px;
  background: #263238;
  color: #cfd8dc;
  z-index: 100;
  padding: 20px;
  left: -250px;
  transition: 0.3s;
}

.switcher-wrap.active {
  left: 0;
}

.switcher-wrap ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

.switcher-wrap ul li {
  margin-bottom: 0.5rem;
}

.switcher-wrap ul li a {
  color: #cfd8dc;
}

.switcher-wrap ul li a:hover {
  color: #04d9ff;
}

.switcher-wrap .color-switcher ul li {
  width: 28px;
  height: 28px;
  float: left;
  margin: 3px;
  margin-bottom: 10px;
  cursor: pointer;
  transition: 0.3s;
}

.switcher-wrap .color-switcher ul li.active {
  border: 3px solid #455a64;
}

.switcher-wrap .switcher-trigger {
  position: absolute;
  left: 100%;
  width: 40px;
  height: 40px;
  background: #263238;
  top: 0;
  font-size: 20px;
  text-align: center;
  color: rgba(29, 233, 182, 0.5);
  cursor: pointer;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
  align-items: center;
  -ms-flex-pack: center;
  justify-content: center;
  border-radius: 0 2px 2px 0;
}

.switcher-wrap .switcher-trigger:hover {
  color: #04d9ff;
}

@media screen and (max-width: 768px) {
  .switcher-wrap {
    display: none;
  }
}

